//
// PAGE TITLE
// Site-wide styles for the page title
//
// --------------------------------------------------

// TODO: Review all code and clean as needed
// TODO: There are extra styles in here that may not belong
// TODO: Need descriptions of what rules do
// TODO: Are all the breakpoints really necessary?

#page-title {
  clear: both;
  display: block;
  padding: 0;
  position: relative;
  z-index: 100;

  @include breakpoint('medium','landscape'){
    margin-bottom: 0;
  }

  @include breakpoint('medium','portrait'){
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 40px;
    min-height: 12px;
    padding: 4px 0;
  }

  @include breakpoint('medium','portrait','ipad'){
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 40px;
    min-height: 12px;
    padding: 4px 0;
  }

  @include breakpoint('small','landscape'){
    margin-bottom: 30px;
  }

  @include breakpoint('small','portrait'){
    margin-bottom: 30px;
  }

  &:after {
    content: ' ';
    clear: both;
    display:block;

  }

  h1 {
    color: $cloudy-gray;
    display: inline-block;
    font-size: 26px;
    font-weight: 600;
    line-height: 1.5;
    min-height: 48px;
    margin: 0px 0px 18px 0px;
    padding: 7px 0 8px;
    text-align: left;
    border-bottom: 1px solid $border-color;
    width: 100%;

    span.shorten-header {
      @include span(8);
      margin: 0;
    }

    @include breakpoint('medium','landscape'){
      @include span(9);
      padding: 7px 0 8px;
      text-align: left;
      width: 100%;
    }

    @include breakpoint('medium','portrait'){
      padding: 4px 0;
      text-align: center;
      width: 100%;

      &.news-page-title, &.article-type {
        padding: 8px 0;
      }
    }

    @include breakpoint('medium','portrait','ipad'){
      padding: 4px 0;
      text-align: center;
      width: 100%;

      &.news-page-title, &.article-type {
        padding: 8px 0;
      }
    }

    @include breakpoint('small','landscape'){
      clear: both;
      display: block;
      font-size: 20px;
      margin-bottom: 0;
      min-height: 48px;
      text-align: center;
      width: 100%;
    }

    @include breakpoint('small','portrait'){
      clear: both;
      display: block;
      float: none;
      font-size: 20px;
      margin-bottom: 0;
      min-height: 48px;
      text-align: center;
      width: 100%;
    }

    @include breakpoint('x-small'){
      clear: both;
      display: block;
      font-size: 1.1rem;
      text-align: center;
      width: 100%;
    }

    &.article-type {
      font-size: 20px;
    }

    span.page-type {
      @include raleway;
      font-size: 20px;
      font-weight: 600;
      color: $charcoal;
      padding-bottom: 15px;
    }

    a, a:visited {
      color: $violet;
      text-decoration: none;
    }

    a:hover, a:focus {
      color: $dark-navy;
      text-decoration: underline;
    }
  }

  .appIconsDetail {

    .addthis_default_style {
      float: right;
      padding: 10px 0;
      width: auto;
      @include breakpoint('small'){
        margin-right: 0;
      }

      a span.fa {
        display: inline-block;
        line-height:1.15;
        padding-top: 5px;
        text-align: center;
      }
    }
  }
}

.page-statuscode-404 div#page-title {
  display: none;
}
.page-statuscode-404 div#sidebar-first {
  display: none;
}

#at_hover.atm-s a:focus, .atm-s a:focus {
  outline: 2px solid $deep-sea;
}

#block-pagetitle h1 {
  @include raleway;
  color: $charcoal;
  font-size: 26px;
  font-style: normal;
  font-weight: normal;
  line-height: 1.4;
  margin-bottom: 18px;

  @include breakpoint('small'){
    margin-bottom: 43px;
    text-align: center;
  }
}

