//
// BLOCKS
// Applies to all blocks on the site
//
// NOTE: If you need to change styles for a specific block, 
// find that block in the blocks directory,
// or create a new file in the blocks directory
// --------------------------------------------------

// TODO: Review all code in this file and move it into the correct places
// TODO: Specific Blocks need to be moved out into new files

.block {
  background: none;
  font-size: 1em;
  position: relative;

  h2 {
    font-size: 1.5em;
    line-height: 1.15;
    padding: 0px 0px 10px;

    @include breakpoint('small','portrait'){
      &.pagelet {
          width: 75%;
      }
    }
  }

  h3 {
    padding: 10px 0;

    &.pagelet {
      position: relative;
      .subscribe-rss {
        position: absolute;
        top:0;
        right: 0;
      }
    }
  }

  header {
    margin-bottom: 10px !important;
    padding: 0;
  }

  hr {
    border-top: 1px solid $border-color;
    margin-top: 5px;
    margin-bottom: 15px;
  }

  h2 {
    @include breakpoint('small','portrait'){
      width: 85%;
    }
    a {
      color: $navy;

      &:hover {
        color: $azure;
        text-decoration: underline;
      }
    }
  }

  table {
    border: 1px solid $border-color;

    th, td {
      border-left: 1px solid $border-color;
      border-right: 1px solid $border-color;
      border-bottom: 1px solid $border-color;
    }
  }

  fieldset legend {
    display: none;
  }

  &.related-materials {
    max-width: 256px;
    h2 {
      border-bottom: 1px solid $border-color;
      font-size: 20px !important;
      padding-bottom: 12px;
      margin-bottom: 12px;
    }
    a {
      word-break: break-word;
    }
  }

  #appsIconsList {
    position: absolute;
    top: 0;
    right: 0;

    .user-action {
      display: inline;
      float:right;

      @include breakpoint('small') {
        clear: both;
        display: inline-block;
        float: none;

        .hide-for-small {
          display: none;
        }
      }

      @include breakpoint('x-small') {
        clear: both;
        display: inline-block;
        float: right;

        .hide-for-small {
          display: none;
        }
      }

      &.fa-icon-download:after {
        @include fontawesome;
        background-color: $cloudy-gray;
        border-radius: 3px;
        color: $white;
        content: "\f019";
        display: inline-block;
        font-size: inherit;
        margin-left: 5px;
        padding: 5px 6px;
        text-decoration: none;

        &:hover {
          background-color: $azure;
        }

      }
    }
  }

  .govDelivery {
    margin: 0 0 2em 0;

    p {
        font-size: 12px;
    }

    .input-wrapper {
      #gov-delivery-box {
        float: left;
        width: inherit;   
      }

      #gov-delivery-submit.buttonSmall {
        float: right;
        font-weight: 300;
        height: 24px;
        margin: 0 !important;
        padding: 4px 10px;
      }
    }
  }

  .subscribe-rss {
    display: inline-block;
    float: right;
    margin-top: -3px;
    text-align: right;
    width: 20%;

    .fa {
      // font-size: 0.85rem;
      // padding: 3px 5px;
      // text-align: center;
      // width: 25px;
      font-size: 15px;
    }
  }

  .block-container {
    display: block;
    position: relative;
    width: 100%;

    .subscribe-rss {
      position: absolute;
      right: 0;
      top: 0;
      z-index: 5;
    }
  }

  .callout, .stylized-box-1, .stylized-box-2, .stylized-box-3 {
    h2.pagelet {
      width: 100% !important;
    }
  }

  h2.pagelet {
      width: 100% !important;
  }

}

.brick-red-grad {
    background-image: -webkit-linear-gradient(to bottom, #b04a4a, #922e2e, #812625, #711b1c, #6c1818, #621213);
    background-image: -moz-linear-gradient(to bottom, #b04a4a, #922e2e, #812625, #711b1c, #6c1818, #621213);
    background-image: -ms-linear-gradient(to bottom, #b04a4a, #922e2e, #812625, #711b1c, #6c1818, #621213);
    background-image: -o-linear-gradient(to bottom, #b04a4a, #922e2e, #812625, #711b1c, #6c1818, #621213);
    background-image: linear-gradient(to bottom, #b04a4a, #922e2e, #812625, #711b1c, #6c1818, #621213);
}

/* Migrated Landing Pages Blocks */
.show-for-sr {
  text-indent: -9999px;
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0
}

/* ----------------------------------------------------------------------
EMAIL SUBSCRIBE
---------------------------------------------------------------------- */
#subscribe .panel {
  background-color:#e9e9e9;
  margin: 0;
  padding: 1.5em;
}

#subscribe h2 {
  color: #273A56;
  font-size:2em;
  font-weight: 300;
  line-height:1;
  margin-bottom: .25em;
}

#subscribe p {
  color: #273A56;
  font-size: 1.2em;
}

#subscribe .row.collapse {
  margin-bottom: 1em;
}

#subscribe input[type='text'] {
  border-radius: 4px;
  -webkit-box-shadow: inset rgba(0, 0, 0, .3) 2px 2px 3px -1px;
  -moz-box-shadow: inset rgba(0, 0, 0, .3) 2px 2px 3px -1px;
  box-shadow: inset rgba(0, 0, 0, .3) 2px 2px 3px -1px;
  color:#b1b1b1;
  height: 40px;
  margin: 0 0 15px;
  padding: .5em;
  width: 100%;
}

#subscribe input[type='text']:focus {
  color:#000;
  /*content:"";*/
}

#subscribe input[type='submit'] {
  background-color: #2f64b2;
  border: none;
  border-radius: 4px;
  box-shadow: none;
  display: block;
  float:right;
  font-size: 1em;
  font-weight: 600;
  height: auto;
  line-height: 1.5;
  margin-bottom: 0;
  padding: 0.5em 1.5em;
  margin-top: 20px;
  -webkit-box-shadow:none;
}

#subscribe input[type='submit']:hover,
#subscribe input[type='submit']:focus {
    background-color: #273A56;
}

